@font-face {
    font-display: swap;
    font-family: 'Pokemon_Hollow';
    font-style: normal;
    src: url('../fonts/Pokemon/Pokemon_Hollow.ttf');
}

@font-face {
    font-display: swap;
    font-family: 'Pokemon_Solid';
    font-style: normal;
    src: url('../fonts/Pokemon/Pokemon_Solid.ttf');
}

/* montserrat-regular - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/Montserrat/montserrat-v25-latin-regular.eot'); /* IE9 Compat Modes */
    src: url('../fonts/Montserrat/montserrat-v25-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../fonts/Montserrat/montserrat-v25-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/Montserrat/montserrat-v25-latin-regular.woff') format('woff'), /* Modern Browsers */
         url('../fonts/Montserrat/montserrat-v25-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../fonts/Montserrat/montserrat-v25-latin-regular.svg#Montserrat') format('svg'); /* Legacy iOS */
  }
  
